.lw-product {
  padding: 60px 0;
  background-color: var(--gray-bg-color);
}
.lw-product__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
}
@media (max-width: 768px) {
  .lw-product__grid {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.lw-product .lw-base-title {
  grid-column: 1/3;
  margin-bottom: 6px;
}

.lw-callback {
  padding: 120px 0 60px;
}
.lw-callback__wrapper {
  display: grid;
  grid-template-columns: 430px auto;
  grid-gap: 20px;
  padding: 50px 30px;
  background-image: url("../assets/img/callback-bg.png");
  background-size: cover;
  background-position: center;
}
@media (max-width: 1024px) {
  .lw-callback__wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .lw-callback__wrapper {
    grid-template-columns: 1fr;
    padding: 40px 20px;
  }
}
@media (max-width: 534px) {
  .lw-callback__wrapper {
    padding: 30px 10px;
  }
}
@media (max-width: 768px) {
  .lw-callback__content {
    padding: 0 40px;
    margin: 0 auto;
  }
}
@media (max-width: 360px) {
  .lw-callback__content {
    padding: 0 10px;
  }
}
.lw-callback__content .lw-base-title {
  color: var(--white);
}
@media (max-width: 768px) {
  .lw-callback__content .lw-base-title {
    width: 100%;
    text-align: center;
  }
}
.lw-callback__content-info-line {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.lw-callback__content-info-line svg {
  width: 100%;
  max-width: 18px;
  height: 18px;
  margin-right: 10px;
  fill: var(--white);
}
.lw-callback__content small {
  padding-left: 28px;
}
.lw-callback__side {
  display: flex;
  gap: 20px;
}
@media (max-width: 768px) {
  .lw-callback__side {
    justify-content: center;
  }
}
.lw-callback__side a {
  height: fit-content;
  margin-top: auto;
}
.lw-callback__side a:first-child {
  margin-left: auto;
}
@media (max-width: 768px) {
  .lw-callback__side a:first-child {
    margin: 0;
  }
}

.lw-company {
  padding: 60px 0;
  background-color: var(--gray-bg-color);
}
.lw-company__top {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  margin-bottom: 15px;
}
@media (max-width: 1024px) {
  .lw-company__top {
    grid-template-columns: 1fr;
  }
}
.lw-company__top-content {
  padding-right: 20px;
}
.lw-company__top-content .lw-base-title {
  margin-bottom: 30px;
}
@media (max-width: 1024px) {
  .lw-company__top-content .lw-base-title {
    text-align: center;
  }
}
.lw-company__top-content .lw-base-text {
  font-size: 14px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 10px;
}
@media (max-width: 1024px) {
  .lw-company__top-content .lw-base-text {
    text-align: center;
  }
}
.lw-company__top-gallery .swiper {
  padding-bottom: 35px;
}
.lw-company__top-gallery .swiper-slide {
  border-radius: 2px;
  overflow: hidden;
}
.lw-company__top-gallery .swiper-slide img {
  display: block;
  width: 100%;
}
.lw-company__bottom {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
@media (max-width: 960px) {
  .lw-company__bottom {
    display: flex;
    flex-direction: column;
  }
}
.lw-company__bottom .lw-activity-card .lw-base-text {
  min-height: 40px;
}
@media (max-width: 960px) {
  .lw-company__bottom .lw-activity-card .lw-base-text {
    min-height: initial;
    margin-bottom: 20px;
  }
}
.lw-company__bottom .lw-activity__title {
  grid-column: 1/5;
  font-weight: 700;
}
@media (max-width: 1024px) {
  .lw-company__bottom .lw-activity__title {
    text-align: center;
  }
}
.lw-company__bottom .lw-activity-card:first-of-type {
  grid-column: 1/3;
}

